<template>
  <div class="page code">
    <div class="header">
      CODE
    </div>
    <div class="sun"></div>
    <div class="dotted-circle"></div>
    <hr>
    <CodeButton v-for='info in options' :info='info' v-bind:key='info.id' @show='show(info.id)'></CodeButton>
    <transition name="slide">
      <Info v-if='show_info' :pages='info_data.pages' @click='clear()'></Info>
    </transition>
    <hr>
  </div>
</template>

<script>
import Info from './../components/modal/modal-project-details.vue';
import CodeButton from './../components/CodeButton.vue';
import CodeJSON from './../json/code.json';

export default {
  props:[],
  data(){
    return{
      options: CodeJSON,
      hover_img: 'sheep.png',
      folder:'./../assets/icons',
      tag:[],
      show_info: false,
      info_data: null,
    }
  },
  computed:{
    image_src:function(){
      var images = require.context('./../assets/icons', false)
      return images("./" + this.hover_img)
    },
  },
  methods:{
    clear:function(){
      this.show_info = false;
      this.info_data = null
    },
    show:function(index){
      this.show_info = true;
      this.info_data = this.options.find((item) => item.id == index);
    }
  },
  components:{
    CodeButton,
    Info
  }
}
</script>
