<template>
    <div class="home">
      <Stars :width='star_canvas_width' :height='star_canvas_height'></Stars>
      <div :class='"circle anim-rotate-left " + animation'>
        <transition name="rotate-over">
          <Radial v-if='value == CONST.CODE' @onClick='showProject'></Radial>
        </transition>
        <div class="circle-inner anim-rotate-right">
          <div class="circle-inner anim-rotate-left-2">
          </div>
        </div>
      </div>
      <div :class="'libra-div ' + value">
        <button class="libra-btn" @click='setView(CONST.ABOUT)'></button>
        <div class="libra-buttons-div">
          <button type="button" class="btn-code" name="button" @click='setView(CONST.CODE)'>CODE</button>
          <button type="button" class="btn-art" name="button" @click="setView(CONST.ART)">ART</button>
        </div>
      </div>
      <transition name="slide">
        <Info v-if='show_info' :pages='info_data.pages' @click='clear()'></Info>
      </transition>
    </div>
</template>

<script>
import Info from './../components/modal/modal-project-details.vue';
import Stars from './../components/Stars.vue';
import Radial from './../components/Radial.vue';
import CodeJSON from './../json/code.json';

export default {
  props:['value'],
  name: 'Home',
  data(){
    return{
      options: CodeJSON,
      CONST:{
        HOME: "home",
        ART: "art",
        CODE: "code",
        ABOUT: "about"
      },
      show_info: false,
      info_data: null,
      vue_canvas: null,
      // padding: 60
      padding: 0,
      star_canvas_width: window.innerWidth,
      star_canvas_height: window.innerHeight
    }
  },
  computed:{
    animation:function(){
      switch (this.value) {
        case this.CONST.ABOUT:
          return "about-slide-down";
      }

      return "about-slide-up";
    }
  },
  methods:{
    setView:function(view){
      this.$emit("setView", view);
    },
    clear:function(){
      this.show_info = false;
      this.info_data = null
    },
    showProject:function(index){
      if(index == null){
        this.$emit("setView", this.CONST.HOME);
      }
      else{
        this.show_info = true;
        this.info_data = this.options.find((item) => item.id == index);
      }
    }
  },
  mounted:function(){},
  components: {
    Stars,
    Radial,
    Info
  }
}
</script>
