<template>
  <div class="code-prev-div">
    <img :src='image_src' alt="">
    <div class="code-prev-details">
      <h2>{{info.title}}</h2>
      <p>{{info.desc}}</p>
      <button type="button" name="button" @click='read_more()'>READ MORE</button>
    </div>
  </div>
</template>

<script>
export default {
  props:["info"],
  data(){
    return{

    }
  },
  computed:{
    image_src:function(){
      var images = require.context('./../assets/icons', false)
      return images("./" + this.info.img)
    },
  },
  methods:{
    read_more:function(){
      this.$emit("show");
    }
  }
}
</script>

